import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)

const constantRouterMap = [
  { path: '/', hidden: true, name: 'ant-admin', redirect: '/index'},
  { path: '/index', component: () => import('@/views/index.vue'), name: 'index'},
  { path: '/map', component: () => import('@/views/map/index.vue'), name: 'map'},
  { path: '/table', component: () => import('@/views/table/index.vue'), name: 'table'},
  { path: '/tree', component: () => import('@/views/tree/index.vue'), name: 'tree'},
  { path: '/address', component: () => import('@/views/addressSet/index.vue'), name: 'setAddress'},
  { path: '/area', component: () => import('@/views/areaSelect/index.vue'), name: 'areaSelect'},
  { path: '/addressSearch', component: () => import('@/views/addressSearch/index.vue'), name: 'addressSearch'},
  { path: '/sourceDialog', component: () => import('@/views/source/dialog.vue'), name: 'source'},
  { path: '/sourceView', component: () => import('@/views/source/view.vue'), name: 'sourceView'},
  { path: '/himage', component: () => import('@/views/hImage/index.vue'), name: 'hImage'},
  { path: '/imageViewer', component: () => import('@/views/imageViewer/index.vue'), name: 'viewer'},
  { path: '/iconFont', component: () => import('@/views/IconFontBox/index.vue'), name: 'iconFont'},
  { path: '/uploadImage', component: () => import('@/views/upload/image.vue'), name: 'UploadImage' },
  { path: '/UploadVideo', component: () => import('@/views/upload/video.vue'), name: 'UploadVideo' },
  { path: '/import', component: () => import('@/views/batchImport/index.vue'), name: 'excelImport' },
  { path: '/addressBook', component: () => import('@/views/addressBook/index.vue'), name: 'addressSet' },
  { path: '/importFile', component: () => import('@/views/file/index.vue'), name: 'ImportFile' },
  { path: '/treeMode', component: () => import('@/views/test/treeMode.vue'), name: 'TreeMode' },
  { path: '/directives/integer', component: () => import('@/views/directives/integer.vue'), name: 'Integer' }
]

const routes = (function() {
  const arg = arguments
  const len = arg.length
  const arr = arg[0]
  if (len === 1) return arr
  for (const i in arg) {
    if (i !== '0') {
      arr.push.apply(arr, arg[i])
    }
  }
  return arr
})(constantRouterMap)

export default new Router({
  scrollBehavior: () => ({ y: 0 }),
  routes: constantRouterMap
})

export const asyncRouterMap = routes
